<template>
  <div class="zero">
    <!-- 顶部图片 -->
    <img class="zero__img-auto" :src="require('@/assets/zeroLive/topBg199.jpg')" />
    <!-- 手机号 -->
    <p
      class="zero__state mt__20 mb__20"
      v-show="stateIsShow"
      @click="stateModelShow=true">《个人信息授权与保护声明》</p>
    <!-- 理解报名按钮 -->
    <div class="zero__form-btn-wrap">
      <div class="mt__20" @click="pay">199元抢购</div>
    </div>
    <div class="zero__line"></div>
    <div class="zero__line-title">最新报名客户</div>
    <VanSwipe
      class="zero__swipe-box"
      :autoplay="3000"
      :show-indicators="false"
      vertical>
      <VanSwipeItem v-for="(item, index) in swipeList" :key="index">
        <div
          v-for="(el, index) in item"
          :key="index"
          class="zero__swipe-box__item">
          <p>{{ el.phone }}</p>
          <p>{{ el.state }}</p>
          <p>{{ el.time }}</p>
        </div>
      </VanSwipeItem>
    </VanSwipe>
    <!-- 底部图片 -->
    <img
      :class="['zero__img-auto', { mt__40: !index }]"
      v-for="(item, index) in detailImg"
      :key="index"
      :src="require(`@/assets/zeroLive/${item}.png`)" />
    <!-- 备案图片 -->
    <img
      src="@/assets/zeroLive/footer_img.png"
      alt=""
      class="footer_img"
      v-if="stateIsShow">
    <!-- 底部按钮 -->
    <div class="zero__footer">
      <div class="zero__footer__topic">
        <span class="zero__footer__topic__title">市场价:<span class="price">199</span>元</span>
      </div>
      <div class="zero__footer__btn" @click="pay"><span>立即报名</span></div>
    </div>
    <Statement
      v-if="stateModelShow"
      @onCloseMask="stateModelShow=false" />
  </div>
</template>

<script>
import { Swipe, SwipeItem } from 'vant'
import swipeList, { originalConfig, codeMap } from './config'
import { getSearch } from '@/utils'
import Statement from '@/components/Statement'
import wxAuth from '@/helper/Auth/wxAuth'
import { mapState } from 'vuex'
export default {
  components: {
    VanSwipe: Swipe,
    VanSwipeItem: SwipeItem,
    Statement
  },
  data () {
    return {
      detailImg: [1, 2, 3, 4, '6c'], // 详情图
      stateIsShow: getSearch('medId') === '1610', // 1610 快手渠道
      stateModelShow: false, // 声明弹窗 快手渠道需要显示此弹窗
      ...originalConfig, // 埋点数据
      swipeList, // 轮播数组
      phone: '', // 手机号
      code: '', // 验证码
      paytype: 1 // 支付类型
    }
  },
  async beforeRouteEnter (to, from, next) {
    // 获取微信授权
    await wxAuth({
      getTokenQuery: { ...getSearch() }
    })
    next()
  },
  computed: {
    ...mapState('user', ['userInfo'])
  },
  async created () {
    // const {
    //   id,
    //   unionid,
    //   wx_body: { openid }
    // } = this.userInfo
    // this.$log.update({
    //   openid,
    //   unionid,
    //   uid: id,
    //   mid: id
    // })
    this.checkOrderInWx()
  },
  methods: {
    checkOrderInWx () {
      return this.$apis.payment
        .checkOrderInWx({ product_sn: 'PRODUCT_SN_XIAOBAI_199_MH_LIVE' })
        .catch((err) => {
          this.payErrorBack(err)
          return Promise.reject(err)
        })
    },
    payErrorBack (data) {
      console.log(data)
      if (data.code === 30009) {
        this.$dialog({
          message: '您已学习过此课程',
          confirmButtonText: '查看老师'
        }).then((res) => {
          console.log(res)
          // this.$log.click(codeMap[data.code].report)
          this.$router.push({
            name: 'QrcodeInWx',
            query: {
              productSn: data.data.product_sn
            }
          })
        })
        return
      }
      if (codeMap[data.code]) {
        this.$dialog({ message: data.msg || codeMap[data.code].msg }).then(() => {
          // this.$log.click(codeMap[data.code].report)
        })
      }
    },
    async pay () {
      // this.$log.click({
      //   current_page: location.href,
      //   module_ori: '表单提交',
      //   module_part: '106_toufang_lcx_0386'
      // })
      this.$apis.userOperator.report()
      this.$apis.payment.payInWx({
        pay_type: 2,
        pay_mode: 3,
        product_sn: 'PRODUCT_SN_XIAOBAI_199_MH_LIVE',
        ...getSearch()
      })
        .then(result => {
          window.WeixinJSBridge.invoke('getBrandWCPayRequest', result, (res) => {
            if (res.err_msg === 'get_brand_wcpay_request:ok') {
              this.$router.push({
                name: 'QrcodeInWx',
                query: {
                  productSn: 'PRODUCT_SN_XIAOBAI_199_MH_LIVE'
                }
              })
            }
          })
        })
        .catch(err => {
          this.payErrorBack(err)
        })
    }
  }
}
</script>
<style lang="scss" scoped>
.zero {
  width: 100%;
  padding-bottom: 120px;

  img {
    vertical-align: middle;
  }

  .head {
    @include verCenter;

    color: #fff;
    font-size: 30px;
    justify-content: space-between;
    height: 85px;
    background: #5f99ff;
    box-shadow: 0 2px 20px 0 rgba(33, 91, 188, 0.2);
  }

  .text__white {
    color: #fff;
  }

  .text__time {
    color: #ffd316;
  }

  .zero__img-auto {
    width: 100%;
  }
  // 备案图片样式
  .footer_img {
    width: 750px;
    height: 140px;
  }

  .form-title {
    font-size: 26px;
    font-weight: 500;
    color: #38465d;
    text-align: center;
    margin-top: 35px;
  }

  .zero__form-item {
    @include verCenter;

    margin-left: auto;
    margin-right: auto;
    width: calc(100% - 160px);
    height: 84px;
    border: 0;
    background: #eef2f8;
    font-size: 34px;
    font-weight: 400;
    font-weight: bold;
    color: #000;
    display: flex;

    * {
      height: 100%;
    }

    img {
      width: 16px;
      height: 13px;
    }

    p {
      @include horVerCenter;
    }

    .phone-code {
      font-size: 34px;
      font-weight: 400;
      color: #666;
    }

    input {
      background-color: transparent;
      border: 0;
      padding-left: 30px;
      width: calc(100% - 230px);

      &::placeholder {
        color: #b9b7b7;
        font-size: 34px;
        font-weight: 400;
      }
    }
  }

  .zero__state {
    font-size: 20px;
    color: #fe3e11;
    text-align: right;
    padding-right: 60px;
  }

  .zero__form-btn-wrap {
    position: relative;
    height: 84px;
    text-align: center;
    margin: 20px 79px 0 80px;

    & > div {
      font-size: 48px;
      font-weight: 400;
      color: #fff;
      text-align: center;
      padding: 18px 0;
      background: linear-gradient(180deg, #ff7754 0%, #fa501e 100%);
      animation: btnScale 1s infinite;
    }
  }

  .zero__line {
    margin: 60px auto 0;
    height: 1px;
    width: calc(100% - 100px);
  }

  .zero__line-title {
    margin-top: 25px;
    font-size: 30px;
    font-weight: 500;
    color: #38465d;
    text-align: center;
  }

  .zero__swipe-box {
    margin-left: 72px;
    height: 100px;
    margin-top: 30px;

    .zero__swipe-box__item {
      width: calc(100% - 100px);
      display: flex;
      justify-content: space-between;
      font-size: 26px;
      font-weight: 400;
      line-height: 50px;
      color: #434c5d;
    }
  }

  .zero__footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 130px;
    background: #fff;
    display: flex;
    border-radius: 20px 20px 0 0;

    .zero__footer__topic {
      width: 53%;

      @include horVerCenter;

      padding: 0 10px;
      justify-content: space-between;
      font-size: 36px;
      border-radius: 20px 0 0 0;

      .zero__footer__topic__title {
        text-align: center;
        font-size: 36px;
        font-family: PingFang SC;
        font-weight: 400;
        font-style: italic;
        color: #fe3e11;

        .price {
          font-size: 90px;
          font-family: DIN;
          font-weight: bold;
        }
      }

      .zero__footer__topic__number {
        @include horVerCenter;

        width: 200px;
        height: 48px;
        font-size: 38px;
        color: #fff;
        font-style: italic;
        background: #fd5b2d;
        border-radius: 24px;
      }

      .zero__footer__topic__money {
        font-style: italic;
        color: #fe3e11;

        & > i {
          font-size: 112px;
          color: #fe3e11;
          font-weight: bold;
        }
      }
    }

    .zero__footer__btn {
      @include horVerCenter;

      width: 60%;
      height: 100%;
      background: linear-gradient(180deg, #ff7754 0%, #fa501e 100%);
      font-size: 48px;
      font-weight: 600;
      color: #fff;
      border-radius: 0 20px 0 0;

      span {
        animation: btnScale 1s infinite;
      }
    }
  }
}

@keyframes btnScale {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}
</style>
